import React, { Component } from "react";
import "./rostik.css";
import Ros from "../../images/lesh.gif";


export default class Account extends Component {
  constructor() {
    super();
    this.state = {
        showImage: false,
      };
  }
  onButtonClickHandler = () => {
    this.setState({showImage: true});
    setTimeout(this.noClick, 1000);
   };
noClick = () => {
    this.setState({showImage: false});
}
  render() {

    return (
<div className="rostik">
    <h1 className="ros-h1">HAPPY BIRTHDAY!!!</h1>
    <h2 className="ros-h2">WE <b>HATE</b>LOVE YOU!</h2>
    {this.state.showImage && <img className="ros-prize" src={Ros} />}
    <br></br>
    <button className="ros" onClick={this.onButtonClickHandler}>ПОЛУЧИТЬ ПОДАРОК</button>
</div>
    );
  }
}
